import { Component, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';

import { AuthoritiesService } from '@authorities/services';

@Component({
  selector: 'j-authorities-nav',
  templateUrl: './authorities-nav.component.html',
  styleUrls: ['./authorities-nav.component.scss'],
})
export class AuthoritiesNavComponent implements OnDestroy {
  links = [
    'users',
    // 'order',
    'otp',
  ];

  subscription = new Subscription();

  constructor(private authoritiesService: AuthoritiesService) {
    this.initSubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(label: string): string {
    return `AUTHORITIES.MAIN.NAV.${label.toUpperCase()}`;
  }

  private initSubscription() {
    this.subscription.add(
      this.authoritiesService.getOtpDevices().subscribe((otpDevices) => {
        if (otpDevices?.length > 0) {
          if (!this.links.includes('otp')) {
            this.links.push('otp');
          }
        } else {
          if (this.links.includes('otp')) {
            this.links.splice(this.links.indexOf('otp'), 1);
          }
        }
      })
    );
  }
}
