import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IAuthorizedPerson } from '@company/company.model';
import { CompanyService } from '@company/company.service';
import { FeatureFlagsService } from '@feature-flags/index';
import { AUTHORITY_TYPE } from '@shared/models';
import { combineLatest, of, Subscription } from 'rxjs';
import { catchError, filter } from 'rxjs/operators';

@Component({
  selector: 'j-authorities-limits',
  templateUrl: './authorities-limits.component.html',
  styleUrls: ['./authorities-limits.component.scss'],
})
export class AuthoritiesLimitsComponent {
  subscription = new Subscription();
  isReady = false;
  person: IAuthorizedPerson;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private companyService: CompanyService
  ) {
    this.initAuthoritiesSubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapAuthority(authority: AUTHORITY_TYPE) {
    return this.mapTranslate(`AUTHORITY.${authority}`);
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.USERS.${key}`;
  }

  private initAuthoritiesSubscription() {
    this.subscription.add(
      combineLatest([
        this.route.params,
        this.companyService
          .getAuthorizedPersons()
          .pipe(filter((list) => list?.length > 0))
          .pipe(
            catchError((err, caught) => {
              console.log(err);

              this.onClose();
              return of([]);
            })
          ),
      ]).subscribe(([params, authorizedPersons]) => {
        this.initPerson(
          authorizedPersons.find((person) => person.id === params.id)
        );
      })
    );
  }

  private async initPerson(person: IAuthorizedPerson) {
    if (!person) {
      this.onClose();
    }

    this.person = person;
    this.isReady = true;
  }

  onClose() {
    this.router.navigate(['/authorities/main']);
  }
}
