import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HasOtpDevicesGuard } from './guards';

import {
  AuthoritiesMainComponent,
  AuthoritiesUsersComponent,
  AuthoritiesOrderComponent,
  AuthoritiesOtpComponent,
  AuthoritiesLimitsComponent,
} from './containers';

import { AuthoritiesComponent } from './authorities.component';

const routes: Routes = [
  {
    path: '',
    component: AuthoritiesComponent,
    data: {
      title: 'SETTINGS.TITLE',
    },
    children: [
      {
        path: 'main',
        component: AuthoritiesMainComponent,
        children: [
          {
            path: 'users',
            component: AuthoritiesUsersComponent,
          },
          {
            path: 'order',
            component: AuthoritiesOrderComponent,
          },
          {
            path: 'otp',
            canActivate: [HasOtpDevicesGuard],
            component: AuthoritiesOtpComponent,
          },
          {
            path: '**',
            redirectTo: 'users',
          },
        ],
      },
      {
        path: 'limits/:id',
        component: AuthoritiesLimitsComponent,
      },
      {
        path: '**',
        redirectTo: 'main',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AuthoritiesRoutingModule {}
