import { Component, Input, OnInit } from '@angular/core';
import {
  AbstractControl,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { CURRENCY } from '@shared/currency/currency.model';
import { MaskedService } from '@shared/masked/masked.service';
import { PAYMENT_TYPE } from '@shared/models';

import { ILimit, LIMIT_MAX_AMOUNT } from '@authorities/models';
import { AuthorityLimitsService } from '@authorities/services';

@Component({
  selector: 'j-authorities-edit-limit-modal',
  templateUrl: './authorities-edit-limit-modal.component.html',
  styleUrls: ['./authorities-edit-limit-modal.component.scss'],
})
export class AuthoritiesEditLimitModalComponent implements OnInit {
  @Input() authorityId: string;
  @Input() paymentType: PAYMENT_TYPE;
  @Input() limit: ILimit;

  title: string;
  isLoading: boolean = false;

  form: FormGroup;

  constructor(
    public modal: NgbActiveModal,
    private authorityLimitsService: AuthorityLimitsService,
    private maskedService: MaskedService
  ) {}

  dismissModal(message: string) {
    this.modal.dismiss(message);
  }

  closeModal(message: string, limit: ILimit = null) {
    this.modal.close({ message, limit });
  }

  ngOnInit() {
    this.title = this.mapTranslate(`CREATE.${this.paymentType}`);

    this.form = new FormGroup({
      amount: new FormControl(
        this.maskedService.getMaskedAmount(this.limit.limit.amount),
        [
          Validators.required,
          (control: AbstractControl) => {
            if (
              control.value &&
              this.maskedService.getNumberedAmount(control.value) >
                LIMIT_MAX_AMOUNT
            ) {
              return { maxAmount: true };
            }
            return null;
          },
        ]
      ),
    });
  }

  async deleteLimit() {
    this.isLoading = true;

    try {
      await this.authorityLimitsService.deleteLimit(
        this.authorityId,
        this.limit
      );
      this.closeModal('delete');
    } catch (error) {
      this.dismissModal('delete');
    } finally {
      this.isLoading = false;
    }
  }

  async updateLimit() {
    const { amount } = this.form.getRawValue();
    const limit: ILimit = {
      ...this.limit,
      limit: {
        amount: this.maskedService.getNumberedAmount(amount),
        currency: CURRENCY.KZT,
      },
    };

    this.isLoading = true;

    try {
      const result = await this.authorityLimitsService.updateLimit(
        this.authorityId,
        limit
      );
      this.closeModal('update', result);
    } catch (error) {
      this.dismissModal('update');
    } finally {
      this.isLoading = false;
    }
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.MODALS.${key}`;
  }
}
