import { Component, Input, OnInit } from '@angular/core';
import {
  AbstractControl,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { CURRENCY } from '@shared/currency/currency.model';
import { MaskedService } from '@shared/masked/masked.service';
import { PAYMENT_TYPE } from '@shared/models';

import { ILimit, LIMIT_MAX_AMOUNT } from '@authorities/models';
import { AuthorityLimitsService } from '@authorities/services';

@Component({
  selector: 'j-authorities-create-limit-modal',
  templateUrl: './authorities-create-limit-modal.component.html',
  styleUrls: ['./authorities-create-limit-modal.component.scss'],
})
export class AuthoritiesCreateLimitModalComponent implements OnInit {
  @Input() authorityId: string;
  @Input() paymentType: PAYMENT_TYPE;

  title: string;
  isLoading: boolean = false;

  form = new FormGroup({
    amount: new FormControl('', [
      Validators.required,
      (control: AbstractControl) => {
        if (
          control.value &&
          this.maskedService.getNumberedAmount(control.value) > LIMIT_MAX_AMOUNT
        ) {
          return { maxAmount: true };
        }
        return null;
      },
    ]),
  });

  constructor(
    public modal: NgbActiveModal,
    private authorityLimitsService: AuthorityLimitsService,
    private maskedService: MaskedService
  ) {}

  ngOnInit() {
    this.title = this.mapTranslate(`CREATE.${this.paymentType}`);
  }

  dismissModal(error: any) {
    this.modal.dismiss(error);
  }

  closeModal(limit: ILimit) {
    this.modal.close(limit);
  }

  async createLimit() {
    this.isLoading = true;

    const { amount } = this.form.getRawValue();
    const limit: ILimit = {
      paymentType: this.paymentType,
      limit: {
        amount: this.maskedService.getNumberedAmount(amount),
        currency: CURRENCY.KZT,
      },
    };

    try {
      const result = await this.authorityLimitsService.createLimit(
        this.authorityId,
        limit
      );

      this.closeModal(result);
    } catch (error) {
      this.dismissModal(error);
    } finally {
      this.isLoading = false;
    }
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.MODALS.${key}`;
  }
}
