import { Component, Input, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { AUTHORITY_TYPE } from '@shared/models';
import { UserService } from '@shared/services';

import { IAuthorizedPerson } from '@company/company.model';
import { CompanyService } from '@company/company.service';
import { AuthoritiesService } from '@authorities/services';
import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';

@Component({
  selector: 'j-authorities-registered',
  templateUrl: './authorities-registered.component.html',
  styleUrls: ['./authorities-registered.component.scss'],
})
export class AuthoritiesRegisteredComponent implements OnDestroy {
  @Input() persons: IAuthorizedPerson[] = [];
  currentUserId: string;
  isPrimaryAuthority: boolean = false;

  headings = ['USER', 'TYPE', 'DURATION'];
  // primaryHeadings = ['RESTRICTIONS', ''];
  primaryHeadings = [''];

  isFeatureDisabled = false;

  subscription = new Subscription();
  restrictions = [];

  constructor(
    private companyService: CompanyService,
    private authoritiesService: AuthoritiesService,
    private featureFlagsService: FeatureFlagsService
  ) {
    this.initCompanySubscription();
    this.initFeatureFlagsSubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapAuthority(authority: AUTHORITY_TYPE) {
    return this.mapTranslate(`AUTHORITY.${authority}`);
  }

  mapRestrictions(restrictionType: string) {
    return this.mapTranslate(`RESTRICTIONS.${restrictionType}`);
  }

  mapTableHeading(heading: string) {
    if (!heading) {
      return '';
    }

    return this.mapTranslate(`TABLE.${heading}`);
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.USERS.${key}`;
  }

  private initCompanySubscription() {
    this.subscription.add(
      this.companyService.refresh$
        .pipe(filter(([, , isAuthorized]) => isAuthorized))
        .subscribe(([user, company]) => {
          this.currentUserId = user?.id;
          this.isPrimaryAuthority = this.companyService.isPrimaryAuthority(
            company.authority
          );

          if (this.isPrimaryAuthority) {
            this.retrieveRestrictions();
          }
        })
    );
  }

  private initFeatureFlagsSubscription() {
    this.subscription.add(
      this.featureFlagsService.featureFlags$.subscribe(() => {
        this.isFeatureDisabled = this.featureFlagsService.isDisabled(
          FEATURE_FLAG.PAYMENT_PERMISSIONS
        );
      })
    );
  }

  private async retrieveRestrictions() {
    this.restrictions = await this.authoritiesService.fetchRestrictions();
  }
}
