import { Component, Input } from '@angular/core';

import { IAuthorizedPerson } from '@company/company.model';

@Component({
  selector: 'j-authorities-unregistered',
  templateUrl: './authorities-unregistered.component.html',
  styleUrls: ['./authorities-unregistered.component.scss'],
})
export class AuthoritiesUnregisteredComponent {
  @Input() persons: IAuthorizedPerson[] = [];

  headings = ['USER', 'IIN', 'PHONE'];

  constructor() {}

  mapTableHeading(heading: string) {
    if (!heading) {
      return '';
    }

    return this.mapTranslate(`TABLE.${heading}`);
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.USERS.${key}`;
  }
}
