import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { AuthoritiesService } from '@authorities/services';
import { CompanyService } from '@company/company.service';

import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class HasOtpDevicesGuard implements CanActivate {
  constructor(
    private authoritiesService: AuthoritiesService,
    private router: Router
  ) {}

  canActivate() {
    return this.authoritiesService.getOtpDevices().pipe(
      map((otpDevices) => {
        const hasDevices = otpDevices?.length > 0;

        if (!hasDevices) {
          this.router.navigate(['/authorities/main']);
        }

        return hasDevices;
      })
    );
  }
}
