import { Component, OnDestroy } from '@angular/core';
import { IAuthorizedPerson } from '@company/company.model';

import { CompanyService } from '@company/company.service';
import { of, Subscription } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Component({
  selector: 'j-authorities-users',
  templateUrl: './authorities-users.component.html',
  styleUrls: ['./authorities-users.component.scss'],
})
export class AuthoritiesUsersComponent implements OnDestroy {
  registered: IAuthorizedPerson[] = [];
  unregistered: IAuthorizedPerson[] = [];

  subscription = new Subscription();

  constructor(private companyService: CompanyService) {
    this.initAuthoritiesSubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private initAuthoritiesSubscription() {
    this.subscription.add(
      this.companyService
        .getAuthorizedPersons()
        .pipe(
          catchError((err, caught) => {
            console.log(err);
            return of([]);
          })
        )
        .subscribe((authorities) => {
          if (authorities?.length > 0) {
            this.registered = authorities.filter(
              (authority) => !!authority.userId
            );
            this.unregistered = authorities.filter(
              (authority) => !authority.userId
            );
          }
        })
    );
  }
}
