import { Component, Input, OnInit } from '@angular/core';

import { PAYMENT_TYPE } from '@shared/models';

import { IAuthorizedPerson } from '@company/company.model';

import { IAuthorityPayment } from '@authorities/models';
import {
  AuthorityPermissionsService,
  AuthorityLimitsService,
} from '@authorities/services';

const PAYMENT_TYPES = [
  PAYMENT_TYPE.PENSION,
  PAYMENT_TYPE.SOCIAL,
  PAYMENT_TYPE.MEDICAL,
  PAYMENT_TYPE.TAX,
  PAYMENT_TYPE.SALARY,
  // PAYMENT_TYPE.IN_BETWEEN,
  PAYMENT_TYPE.CONVERSION,
  PAYMENT_TYPE.FOREIGN,
];

@Component({
  selector: 'j-authorities-payments',
  templateUrl: './authorities-payments.component.html',
  styleUrls: ['./authorities-payments.component.scss'],
})
export class AuthoritiesPaymentsComponent implements OnInit {
  @Input() person: IAuthorizedPerson;

  payments: IAuthorityPayment[];

  isLoading = true;

  constructor(
    private authorityPermissionsService: AuthorityPermissionsService,
    private authorityLimitsService: AuthorityLimitsService
  ) {}

  ngOnInit() {
    this.loadData();
  }

  private async loadData() {
    try {
      const permissions = await this.authorityPermissionsService.getPermissions(
        this.person.id
      );
      const limits = await this.loadLimits();

      this.payments = PAYMENT_TYPES.map((type) => {
        const permission = permissions.find(
          (permission) => permission.paymentType === type
        );
        const limit = limits.find((limit) => limit.paymentType === type);

        return {
          type,
          permissionId: permission ? permission.id : null,
          isEnabled: permission ? permission.permissions.length > 0 : true,
          limitId: limit ? limit.id : null,
          limitAmount: limit ? limit.limit : null,
          isLimitDisabled: [
            PAYMENT_TYPE.CONVERSION,
            PAYMENT_TYPE.FOREIGN,
          ].includes(type),
        };
      });
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }

  private async loadLimits() {
    try {
      const limits = await this.authorityLimitsService.getLimits(
        this.person.id
      );
      return limits;
    } catch (error) {
      return [];
    }
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.PAYMENTS.${key}`;
  }
}
