import { Component, Input } from '@angular/core';

import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { IAuthorizedPerson } from '@company/company.model';
import { PAYMENT_TYPE } from '@shared/models';

import {
  AuthoritiesCreateLimitModalComponent,
  AuthoritiesEditLimitModalComponent,
} from '@authorities/modals';
import { IAuthorityPayment } from '@authorities/models';
import { AuthorityPermissionsService } from '@authorities/services';

@Component({
  selector: 'j-authorities-payment',
  templateUrl: './authorities-payment.component.html',
  styleUrls: ['./authorities-payment.component.scss'],
})
export class AuthoritiesPaymentComponent {
  @Input() payment: IAuthorityPayment;
  @Input() person: IAuthorizedPerson;

  constructor(
    private authorityPermissionsService: AuthorityPermissionsService,
    private notifierService: NotifierService,
    private translateService: TranslateService,
    private modalService: NgbModal
  ) {}

  async onChange(value: boolean) {
    try {
      let permission;

      if (value) {
        permission = await this.enablePayment();
      } else {
        permission = await this.disablePayment();
      }

      if (!this.payment.permissionId && permission?.id) {
        this.payment.permissionId = permission.id;
      }
    } catch (error) {
      this.payment.isEnabled = !this.payment.isEnabled;
    }
  }

  async enablePayment() {
    try {
      const permission = await this.authorityPermissionsService.grantPermission(
        this.person,
        this.payment
      );

      this.notify('ACCESS_ENABLED');
      return permission;
    } catch (error) {
      console.log(error);
      
      this.notify('ACCESS_ENABLE_FAILED', 'error');
      throw error;
    }
  }

  async disablePayment() {
    try {
      const permission =
        await this.authorityPermissionsService.refusePermission(
          this.person,
          this.payment
        );

      this.notify('ACCESS_DISABLED');
      return permission;
    } catch (error) {
      console.log(error);

      this.notify('ACCESS_DISABLE_FAILED', 'error');
      throw error;
    }
  }

  async showCreateLimitModal() {
    const modalRef = this.createModalRef(AuthoritiesCreateLimitModalComponent);

    try {
      const limit = await modalRef.result;

      this.payment.limitId = limit.id;
      this.payment.limitAmount = limit.limit;

      this.notify('LIMIT_SET');
    } catch (error) {
      if (error) {
        this.notify('LIMIT_SET_FAILED', 'error');
      }
    }
  }

  async showEditLimitModal() {
    const modalRef = this.createModalRef(AuthoritiesEditLimitModalComponent);
    modalRef.componentInstance.limit = {
      id: this.payment.limitId,
      paymentType: this.payment.type,
      limit: this.payment.limitAmount,
    };

    try {
      const { message, limit } = await modalRef.result;

      if (message === 'update') {
        this.payment.limitId = limit.id;
        this.payment.limitAmount = limit.limit;

        this.notify('LIMIT_UPDATED');
      } else {
        this.payment.limitId = null;
        this.payment.limitAmount = null;

        this.notify('LIMIT_DELETED');
      }
    } catch (error) {
      if (error === 'update') {
        this.notify('LIMIT_UPDATE_FAILED', 'error');
      } else if (error == 'delete') {
        this.notify('LIMIT_DELETE_FAILED', 'error');
      }
    }
  }

  mapPaymentType(type: PAYMENT_TYPE) {
    return this.mapTranslate(`TYPE.${type}`);
  }

  mapButtonLabel(message: string) {
    return this.mapTranslate(`BUTTON.${message}`);
  }

  mapSwitchMessage(message: string) {
    return this.mapTranslate(`SWITCH.${message}`);
  }

  private createModalRef(modalComponent) {
    const modalRef = this.modalService.open(modalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_dictionary',
      centered: true,
    });

    modalRef.componentInstance.authorityId = this.person.id;
    modalRef.componentInstance.paymentType = this.payment.type;

    return modalRef;
  }

  private mapTranslate(key: string) {
    return `AUTHORITIES.PAYMENTS.${key}`;
  }

  private notify(message: string, type: string = 'default') {
    this.notifierService.notify(
      type,
      this.translateService.instant(this.mapTranslate(`MESSAGES.${message}`), {
        paymentType: this.translateService.instant(
          this.mapPaymentType(this.payment.type)
        ),
      })
    );
  }
}
