import { Component, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';

import { AuthoritiesService } from '@authorities/services';
import { IOtpDevice } from '@company/company.model';

@Component({
  selector: 'j-authorities-otp',
  templateUrl: './authorities-otp.component.html',
  styleUrls: ['./authorities-otp.component.scss'],
})
export class AuthoritiesOtpComponent implements OnDestroy {
  otpDevices: IOtpDevice[] = [];
  headings = ['SERIAL', 'USER'];

  subscription = new Subscription();

  constructor(private authoritiesService: AuthoritiesService) {
    this.initSubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTableHeading(heading: string) {
    if (!heading) {
      return '';
    }

    return this.mapTranslate(`TABLE.${heading}`);
  }

  mapTranslate(key: string) {
    return `AUTHORITIES.OTP.${key}`;
  }

  private initSubscription() {
    this.subscription.add(
      this.authoritiesService.getOtpDevices().subscribe((otpDevices) => {
        this.otpDevices = otpDevices;
      })
    );
  }
}
